import { DateFormat, ForceMoney, ForceMoneyFour } from '@/libs/common';
import { opt } from '@/libs/dic';
import iView from 'iview';
import merge from 'lodash.merge';
import PoptipTable from '@/components/poptip-table.vue';

export default {
    methods: {
        renderDate (h, params, convert = (val => val)) {
            return this.renderDateFormat(h, params, convert)();
        },
        renderDateFormat (h, params, convert = (val => val)) {
            return (format) => h('span', DateFormat(convert(params.row[params.column.key]), format));
        },
        renderDateRange (h, params) {
            return (start, end) => h('span', `${DateFormat(params.row[start])}~${DateFormat(params.row[end])}`);
        },
        renderAppend (h, params) {
            return (append, convert = (val => val)) => h('span', `${convert(params.row[params.column.key])}${append}`);
        },
        renderSuAppend (h, params) {
            return (append, convert = (val => val)) => h('span', `${append}${convert(params.row[params.column.key])}`);
        },
        renderStatus (h, params) {
            return this.renderStatusDiy(h, params)();
        },
        renderMoney (h, params, convert = (val => val)) {
            return <span><iView.Icon style="position: relative; top: -1px;" type="logo-yen"/>{ ForceMoney(Number(convert(params.row[params.column.key]))) }</span>;
        },
        renderMoneyFour (h, params, convert = (val => val)) {
            return <span><iView.Icon style="position: relative; top: -1px;" type="logo-yen"/>{ ForceMoneyFour(Number(convert(params.row[params.column.key]))) }</span>;
        },
        renderStatusDiy (h, params) {
            let status = params.row[params.column.key];
            return (trueTxt = '启用', falseTxt = '禁用', convert = (status => status)) => {
                status = convert(status);
                return (
                    <iView.Tag type='dot' color={status === true ? 'success' : 'error'}>
                        {status === true ? trueTxt : falseTxt}
                    </iView.Tag>
                );
            };
        },
        renderStatusDic (h, params) {
            const status = params.row[params.column.key];
            return (color, dic) => <iView.Tag type='border' color={color[status]}>
                {opt(dic, status).name}
            </iView.Tag>;
        },
        renderDic (h, params) {
            return (dic, cfg = { field: 'id', value: 'name' }) => h('span', opt(dic, params.row[params.column.key], { [cfg.value]: '' }, cfg.field)[cfg.value]);
        },
        renderListPop (h, params, convert = (val => val)) {
            let value = convert(params.row[params.column.key]);
            value = Array.isArray(value) ? value : Array.of(value);
            return (
                <iView.Poptip trigger='hover' placement='bottom'>
                    <iView.Tag>
                        {value.length}
                    </iView.Tag>
                    <div slot='content'>
                        <ul>
                            {value.map(item => <li style='text-align: center; padding: 4px;'>
                                {item}
                            </li>)}
                        </ul>
                    </div>
                </iView.Poptip>
            );
        },
        renderPopTable (h, params, columns, config = {}) {
            const value = params.row[params.column.key];
            config = merge({ params: { value, params }, poptip: { transfer: true, wordWrap: true } }, config);
            const cfg = config.poptip;
            Reflect.deleteProperty(config, 'poptip');
            return (handler = () => params.row, name = value) => <PoptipTable
                { ...{ props: config } }
                name={name}
                columns={columns}
                handler={handler}
                config={{ props: cfg }}/>;
        },
        renderJoin (h, params, split = '/') {
            return (...args) => h('span', args.map(arg => params.row[arg]).join(split));
        },
        renderLink (h, params, fn, convert = (val => val)) {
            return <a onClick={fn}>
                {convert(params.row[params.column.key])}
            </a>;
        },
        renderBtnPop (h, title, text, fn, config) {
            return config.show === false ? null : (
                <iView.Poptip
                    confirm={true}
                    title={title}
                    placement='top'
                    transfer={true}
                    onOn-ok={fn}>
                    <iView.Button { ...merge({ props: { size: 'small' } }, config) }>
                        {text}
                    </iView.Button>
                </iView.Poptip>
            );
        },
        renderActions (h, params, actions) {
            const isEmpty = actions.every(action => action.config?.show === false);
            return isEmpty ? <div>无可用操作</div> : actions.map(action => {
                const { config = {}, text, click } = action;
                const cfg = { props: config, style: config.style };
                switch (action.type) {
                    case 'delete':
                        const { title = '您确定要删除这条数据吗？' } = action;
                        return this.renderBtnPop(h, title, text || '删除', click, merge({ props: { type: 'error' } }, cfg));
                    case 'switch':
                        const { falseText = '禁用', trueText = '启用', bool } = action;
                        const name = bool ? falseText : trueText;
                        const type = bool ? 'warning' : 'success';
                        return this.renderBtnPop(h, `您确定要${name}这条数据吗?`, name, click, merge({ props: { type } }, cfg));
                    case 'resetPassword':
                        const { epName } = action;
                        return this.renderBtnPop(h, `您确定要重置${epName}管理员的密码吗?`, text || '重置密码', click, merge({ props: { type: 'primary' } }, cfg));
                    default:
                        return config.show === false ? null : <iView.Button { ...merge({ props: { size: 'small', type: 'primary' } }, cfg) } onClick={click}>
                            {text}
                        </iView.Button>;
                }
            });
        }
    }
};
