<script>
import iView from 'iview';
export default {
    name: 'PoptipTable',
    props: {
        name: {
            type: [String, Function, Number],
            default: ''
        },
        columns: {
            type: Array,
            required: true
        },
        handler: {
            type: Function,
            required: true
        },
        cache: {
            type: Boolean,
            default: true
        },
        params: {
            type: Object
        },
        gutter: {
            type: Number,
            default: 0
        },
        config: {
            type: Object
        }
    },
    data () {
        return {
            data: {},
            show: false,
            isLoad: false
        };
    },
    methods: {
        async showHandler () {
            if (!this.isLoad || !this.cache) {
                this.show = false;
                this.data = await this.handler(this.params);
                this.isLoad = true;
                this.show = true;
            }
        },
        columnValue (column) {
            let val = this.data[column.key];
            if (typeof column.convert === 'function') {
                val = column.convert(val, this.data, column);
            }
            return val;
        }
    },
    render (h, ctx) {
        return (
            <iView.Poptip {...this.config} onOn-popper-show={ this.showHandler }>
                { typeof this.name === 'function' ? this.name(h) : <a>{ this.name }</a> }
                { this.show ? <div slot="content" style={ this.config.props.wordWrap ? 'word-wrap: break-word' : '' }>
                    { this.columns.map((column, index) =>
                        <div>
                            <iView.Row key={ index } gutter={ this.gutter }>
                                <i-col className={ column.titleClass } span={ column.labelSpan || 24 - (column.valueSpan || 12) }>
                                    {
                                        typeof column.titleRender === 'function'
                                            ? column.titleRender(h, { row: this.data, column }, column.title)
                                            : column.title
                                    }
                                </i-col>
                                <i-col className={ column.valueClass } span={ column.valueSpan || 24 - (column.labelSpan || 12) }>
                                    {
                                        typeof column.render === 'function'
                                            ? column.render(h, { row: this.data, column }, this.columnValue(column))
                                            : this.columnValue(column)
                                    }
                                </i-col>
                            </iView.Row>
                            <iView.Divider style="margin: 5px 0;" />
                        </div>
                    ) }
                </div> : <iView.Spin slot="content" fix></iView.Spin>}

            </iView.Poptip>
        );
    }
};
</script>
