<template>
    <div>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">子产品名:</label>
            <Input v-model="table.query.productSubName" placeholder clearable style="width: 200px"/>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>

        <Row class="margin-top-10">
            <Table :columns="table.cols" :data="table.data"></Table>
            <div style="margin: 10px;overflow: hidden">
                <div style="float: right;">
                    <Page
                        :total="table.total"
                        :show-sizer="true"
                        placement="top"
                        @on-page-size-change="changePageSize"
                        @on-change="changePage"
                    ></Page>
                </div>
            </div>
        </Row>
        <Modal
            v-model="modal"
            ok-text="上架"
            :styles="{width: '900px',height: '600px'}"
            title="渠道上架"
            loading
            @on-ok="upOperation"
        >
            <Table
                @on-selection-change="handleSelectAll"
                :columns="cols1"
                :data="upChannels"
                style="margin-top:20px"
            ></Table>
        </Modal>

        <Modal
            v-model="modal1"
            ok-text="下架"
            :styles="{width: '900px',height: '600px'}"
            title="渠道下架"
            loading
            @on-ok="downOperation"
        >
            <Table
                @on-selection-change="handleSelectAll"
                :columns="cols1"
                :data="upChannels"
                style="margin-top:20px"
            ></Table>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
export default {
    name: 'DistScenery',
    mixins: [TableColRender, TableDataView, ModelView],
    data () {
        return {
            modal: false,
            modal1: false,
            cols1: [
                {
                    type: 'selection',
                    width: 60,
                    align: 'center'
                },
                {
                    title: '组名',
                    key: 'name'
                },
                {
                    title: '利润类型',
                    key: 'profitType',
                    render: (h, params) => (
                        <span> {params.row.profit === 1 ? '百分比' : '数值'} </span>
                    )
                },
                {
                    title: '利润值',
                    key: 'profit'
                },
                {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }
                // {
                //   title: "操作",
                //   render: (h, params) =>
                //     this.renderActions(h, params, [
                //       {
                //         config: {
                //           loading: this.loadingBtn,
                //           style: { marginRight: "5px" }
                //         },
                //         click: () => this.UpSub2Channel(params.row),
                //         text: "上架"
                //       }
                //     ])
                // }
            ],
            selectionParam: [],
            productSubId: null,
            removeUrl: '/api/product/plan/delete',
            updateUrl: '/api/product/plan/update',
            validate: {},
            vo: {
                marketPrice: null,
                lowPrice: null,
                settlePrice: null,
                stock: null
            },
            upChannels: [],
            table: {
                cols: [
                    {
                        title: '产品名',
                        key: 'name'
                    },
                    {
                        title: '子产品名',
                        key: 'subName'
                    },
                    {
                        title: '供应商名',
                        key: 'epName'
                    },
                    {
                        title: '操作时间',
                        key: 'createTime',
                        render: this.renderDate
                    },

                    {
                        title: '操作',
                        width: 200,
                        align: 'center',
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.saleUp(params.row),
                                    text: '渠道上架'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.saleDown(params.row),
                                    text: '渠道下架'
                                }
                            ])
                    }
                ],
                url: 'api/product/distribute/list',
                query: {
                    productSubName: null
                }
            }
        };
    },
    methods: {
        async saleUp (item) {
            const res = await this.get('api/channel/group/sub/channel/list', {
                productSubId: item.productSubId
            });
            this.productSubId = item.productSubId;
            if (res.success) {
                this.upChannels = res.value;
                this.modal = true;
            }
        },
        async downOperation () {
            if (this.selectionParam.length === 0) {
                this.$Message.error('请选择下架的产品');
                return;
            }
            const res = await this.post('api/channel/group/sub/channel/down', {
                productSubId: this.productSubId,
                channels: this.selectionParam
            });
            if (res.success) {
                this.$Message.info('渠道下架成功');
                this.selectionParam = [];
                this.modal1 = false;
            }
        },
        handleSelectAll (selection) {
            this.selectionParam = selection;
        },
        async upOperation () {
            if (this.selectionParam.length === 0) {
                this.$Message.error('请选择上架产品');
                return;
            }
            const res = await this.post('api/channel/group/sub/channel/up', {
                productSubId: this.productSubId,
                channels: this.selectionParam
            });
            if (res.success) {
                this.$Message.info('上架成功');
                this.selectionParam = [];
                this.modal = false;
            }
        },
        async UpSub2Channel (row) {
            this.post();
        },
        async saleDown (item) {
            const res = await this.get('api/channel/group/sub/channel/down/list', {
                productSubId: item.productSubId
            });
            this.productSubId = item.productSubId;
            if (res.success) {
                this.upChannels = res.value;
                this.modal1 = true;
            }
        }
    }
};
</script>
